.App {
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  /*min-height: 100vh;*/
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  font-size: calc(10px + 2vmin);
  padding-top: 6px;
  padding-bottom: 6px;
  padding-left: 15px;
  padding-right: 15px;
  color: white;
}
ul{
  list-style: none;
  text-align: start;
  padding: 0;
}
li{
  margin-top: 5px;
  margin-bottom: 5px;
}
p{
  margin-top: 0;
  margin-bottom: 0;
}
.App-link {
  color: #61dafb;
}
h1{
  margin-top: 10px;
  margin-bottom: 3px;
}
h2{
  margin-top: 3px;
  margin-bottom: 3px;

}
main{
  height: 100%;
  color: white;
  padding: 15px;
  background-color: #202030;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.modal-overlay{
  background-color: rgba(0,0,0,0.7);
  height: 100vh;
  width: 100vw;
  display: flex;
  align-items: center;
  justify-content: center;
  /* padding: 10%; */
  position: fixed;
}

.modal{
  border-radius: 8px;
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  width: 80%;
  height: 80%;
  color: white;
  border: 2px solid white;
}

.modal-header{
  padding: 6px 15px;
  height: 50px;
  width: calc(100% - 30px);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid white;
}

.modal-header>p:first-child{
  font-size: 28px;
}

.modal-header>p:last-child{
  cursor: pointer;
  margin-right: 16px;
  font-size: 40px;
}

.modal-body{
  height: calc(100% - 50px);
  width: 100%;
  padding: 15px;
}

.modal-body>ul{
  width: calc(100% - 30px);
}
